<template>
	<view class="page">
		<view class="page-block super-hot">
			<view class="hot-title-wapper">
				<image class="search-box-icon" src="../../static/images/arrows.png" @tap="goBack()"></image>
				<view class="hot-title">
					歌曲播放
				</view>
			</view>
		</view>
		<!-- <imt-audio autoplay continue :src="audio[now]" :duration="audio[now].duration" @prev="now = now === 0?audio.length-1:now-1"
		 @next="now = now === audio.length-1?0:now+1"></imt-audio> -->
		 <imt-audio autoplay continue :src="audionew"></imt-audio>
		<!-- 
			 src: String 音频链接*必须*
			 autoplay: Boolean 是否自动播放*默认false*
			 continue: Boolean 播放完成后是否继续播放下一首，需定义@next事件*默认false*
			 control: Boolean 是否需要上一曲/下一曲按钮*默认true*
			 color: String 主色调*默认#169af3*
			 @prev:点击上一首按钮
			 @next:点击下一首按钮
		  -->
<!-- 		<view class="list" :class="{active:key===now}" v-for="(item,key) in audio" :key="key" @click="now = key">{{key+1}}</view> -->
	</view>
</template>

<script>
	import imtAudio from 'components/imt-audio/imt-audio'
	export default {
		data() {
			return {
				// audio: [
				// 	'http://m7.music.126.net/20201214140731/c4bb3cf2f69c72e3be410d991e04124d/ymusic/obj/w5zDlMODwrDDiGjCn8Ky/3216893992/9c48/3a94/c011/dd36dbbc99f7c09a1937d4651436548c.mp3',
				// 	'http://m7.music.126.net/20201214135918/6e9dc2c9a41f7197d6f17f0f877b9f8b/ymusic/0fd6/4f65/43ed/a8772889f38dfcb91c04da915b301617.mp3'
				// ],
				audionew: '',
				// now: 0,
			}
		},
		onLoad(params) {
			var me = this;
			var serverUrl = me.serverUrl;
			var songid = params.songid;
			// 请求热搜数据
			uni.request({
				url: serverUrl + '/song/url?id=' + songid,
				method: "POST",
				success: (res) => {
					if (res.data.code == 200) {
						var audionew = res.data.data[0].url;
						me.audionew = audionew;
					}
				}
			});
		},
		methods: {
			goBack() {
				history.back()
			}
		},
		components: {
			imtAudio
		}
	}
</script>

<style>
	page {
		background: #f5f5f5;
	}
	
	.content {
		padding: 20upx;
	}
	
	.list {
		font-size: 28upx;
		line-height: 88upx;
		padding-left: 30upx;
		background: #fff;
		border-radius: 10upx;
		margin-top: 20upx;
		color: #333;
	}
	
	.active {
		background: #169af3;
		color: #fff;
	}
	
	.search-box-icon {
		padding-top: 15px;
		width: 26px;
		height: 26px;
	}
	
	.hot-title {
		font-size: 20px;
		margin-left: 20upx;
		font-weight: bold;
		margin-top: 30upx;
	}
</style>
